'use client'
import Image from 'next/image';
import { useRouter } from 'next/navigation';
import star from './../../public/images/star.png'
import { useEffect, useState } from 'react';
import axios from 'axios';

interface MovieData { // Same interface as above
    movie_id: number;
    title: string;
    description: string;
    rating: number;
    release: number;
    director: string;
    categories: number[];
    image?: string; 
}

interface Category { // Interface for Category data
    category_id: number;
    title: string;
}

const MovieCard : React.FC<{ data: MovieData }> = ({ data }) => {
    const { title, movie_id, categories, rating, image, description, director } = data; // Destructure all the necessary properties
    const route = useRouter();
    const [categoryTitles, setCategoryTitles] = useState<Category[]>([]);

    useEffect(() => {
        const fetchCategoryTitles = async () => {
            if (categories && categories.length > 0) { // Check if categories exists
                try {
                    const response = await axios.get('http://localhost:8000/category'); // Fetch all categories
                    if (response.status === 200) {
                      setCategoryTitles(response.data);
                    } else {
                      console.error('Failed to fetch categories:', response.status);
                    }
                } catch (error) {
                    console.error('Error fetching categories:', error);
                }
            }
        };

        fetchCategoryTitles();
    }, [categories]);

    const CardClickFun =  (id:number) => {
        route.push(`movie/${id}`)
    };
      
    return (
        <div>
            <div onClick={() =>CardClickFun(movie_id)}  // Use movie_id
            className="border-y-4 flex flex-col justify-center items-center bg-slate-100 gap-3 md:max-w-96 p-4 hover:text-yellow-500">
                {image ? (
                    <Image 
                        src={image} 
                        width={200} height={200} alt={title} 
                        className="object-cover w-[200px] h-[200px] rounded-lg m-2 cursor-pointer hover:scale-[1.01]" 
                    />
                ) : (
                    <div className="w-[200px] h-[200px] bg-gray-300 rounded-lg m-2">
                        No Image Available
                    </div>
                )}
                
                <div className="flex items-center justify-start m-2">
                    <Image src={star} alt='Star image' height={20} width={20} />
                    <h3 className='text-lg m-2'> {rating} </h3>
                </div>
                <h1 className="font-bold text-lg">{title}</h1>
                <ol className="flex gap-2 p-4">
                    {categories && categories.length > 0 ? (
                        categories.map((catId, index1) => {
                            const category = categoryTitles.find(cat => cat.category_id === catId); // Find title
                            const catTitle = category ? category.title : catId; // Display title or ID if not found
                            return (
                                <li
                                    className="bg-slate-200 p-2 text-base font-bold"
                                    key={index1}
                                >
                                    {catTitle}
                                </li>
                            );
                        })
                    ) : null}
                </ol>
                <p>{description}</p>
                {/* <p>Director: {director}</p> */}
            </div>
        </div>
    );
}

export default MovieCard;